export default {
	data() {
		return {
			source: 0,
			isSelect :false,
			addressList: [
				{
					id:1,
					name: '刘晓晓',
					mobile: '18666666666',
					addressName: '贵族皇仕牛排(东城店)',
					address: '北京市东城区',
					area: 'B区',
					default: true
				},{
					id:2,
					name: '刘大大',
					mobile: '18667766666',
					addressName: '龙回1区12号楼',
					address: '山东省济南市历城区',
					area: '西单元302',
					default: false,
				}
			],
		}
	},
	onLoad(option){
		console.log(option.source);
		this.source = option.source;
		if(option.source == 1)
		{
			this.isSelect = true;
		}
	},
	methods: {
		//选择地址
		select(row){
			//是否需要返回地址(从订单确认页跳过来选收货地址)
			if(!this.isSelect){
				return ;
			}
			uni.setStorage({
				key:'selectAddress',
				data:row,
				success() {
					uni.navigateBack();
				}
			})
		},
		addAddress(type, item){
			uni.navigateTo({
				url: `/pages/shop/address/addressManage?type=${type}&data=${JSON.stringify(item)}`
			})
		},
		//添加或修改成功之后回调
		refreshList(data, type){
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			
			console.log(data, type);
		}
	}
}
